
.remind-info {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  line-height: 1.0;
  padding: 20px 0px;
  text-align: center;
  z-index: 999;
  color: #fff;
  background: #2f96b4;
}
.remind, .remind-success {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  line-height: 1.0;
  padding: 20px 0px;
  text-align: center;
  z-index: 999;
  color: #fff;
  background: #51a351;
}
.remind, .remind-warn {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  line-height: 1.0;
  padding: 20px 0px;
  text-align: center;
  z-index: 999;
  color: #fff;
  background: #f89406;
}
.remind, .remind-error {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  line-height: 1.0;
  padding: 20px 0px;
  text-align: center;
  z-index: 999;
  color: #fff;
  background: #bd362f;
}
.fade-transition {
  transition: opacity .3s ease;
}
.fade-enter, .fade-leave {
  opacity: 0;
}



