
html {
  height: 100%;
}
body {
  height: 100%;
  background: #f4dddd;
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  position: relative;
}
footer {
  height: 0.96rem;
  text-align: center;
  line-height: 0.96rem;
  background: #050001;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.2;
  font-size: 0.44rem;
}
footer a {
    text-decoration: none;
    color: #fff;
}
footer a:visited {
      color: #fff;
      text-decoration: none;
}
#audio {
  display: none;
}



